import { CULTURAS_PREHISPANICAS, TIPOS_HISTORIA } from './culturalData';

export const createCulturalStoryPrompt = ({ 
  cultura, 
  tipoHistoria, 
  personajesPrincipales, 
  lugar, 
  tema, 
  edad,
  duracion,
  incluirDecisiones 
}) => {
  const culturaData = CULTURAS_PREHISPANICAS[cultura];
  const tipoDesc = TIPOS_HISTORIA[tipoHistoria];
  
  const contextoCultural = `
CONTEXTO CULTURAL ESPECFICO:
- Civilizacin: ${culturaData.nombre}
- Descripcin: ${culturaData.descripcion}
- Elementos culturales autnticos: ${culturaData.elementos.join(', ')}
- Lugares caractersticos: ${culturaData.lugares.join(', ')}
- Personajes tradicionales: ${culturaData.personajes.join(', ')}
`;

  const instruccionesEspeciales = `
INSTRUCCIONES CULTURALES CRTICAS:
1. RESPETAR la autenticidad histrica y cultural
2. USAR elementos, nombres y conceptos genuinos de la cultura ${culturaData.nombre}
3. EVITAR anacronismos o elementos de otras culturas
4. INCORPORAR valores y cosmovisin de la civilizacin elegida
5. USAR un lenguaje respetuoso y educativo sobre la cultura ancestral
`;

  const formatoInteractivo = incluirDecisiones ? `
FORMATO INTERACTIVO:
- Incluir exactamente 3 momentos de decisin marcados como [DECISIN]
- Cada decisin debe tener 2-3 opciones claras
- Las decisiones deben influir en el desarrollo de la historia
- Formato: [DECISIN: Qu debe hacer el protagonista?]
  Opcin A: [descripcin]
  Opcin B: [descripcin]
  Opcin C: [descripcin]
` : '';

  return `
Eres un narrador experto en culturas prehispnicas mexicanas. Crea un ${tipoDesc} de la cultura ${culturaData.nombre} con estas especificaciones:

${contextoCultural}

PARMETROS DE LA HISTORIA:
- Personajes principales: ${personajesPrincipales}
- Lugar principal: ${lugar}
- Tema central: ${tema}
- Edad del pblico: ${edad} aos
- Duracin aproximada: ${duracion} minutos de lectura
- Tipo de historia: ${tipoDesc}

${instruccionesEspeciales}

${formatoInteractivo}

ESTRUCTURA REQUERIDA:
1. Ttulo inspirado en la cultura ${culturaData.nombre}
2. Inicio que establezca el contexto cultural
3. Desarrollo con elementos autnticos de la civilizacin
4. Clmax con resolucin satisfactoria
5. Final que refuerce valores culturales

ELEMENTOS A INCLUIR:
- Nombres en idioma original (nhuatl, maya, zapoteco, etc.) con explicacin
- Descripciones de vestimenta, arquitectura y costumbres autnticas
- Referencias a la cosmovisin y religin de la cultura
- Enseanzas o valores propios de la civilizacin

LONGITUD: Aproximadamente ${duracion === 'corta' ? '300-400' : duracion === 'media' ? '500-700' : '800-1000'} palabras.

Crea una historia memorable que honre la riqueza cultural de ${culturaData.nombre} y eduque sobre su legado.
`;
};

export const createDecisionPrompt = (storyContext, currentDecision, selectedOption) => {
  return `
Contina esta historia cultural basndote en la decisin del usuario:

CONTEXTO PREVIO:
${storyContext}

DECISIN TOMADA:
${selectedOption}

INSTRUCCIONES:
1. Contina la narrativa de forma natural incorporando la decisin
2. Mantn la autenticidad cultural establecida
3. Desarrolla las consecuencias de la eleccin de manera interesante
4. Si es apropiado, incluye una nueva decisin para el usuario
5. Conserva el tono y estilo narrativo anterior

Contina la historia (200-300 palabras):
`;
};